import React from 'react'

function TodoItemButton({img, large, buttonType, handleClick,}) {
  return (
    <button className={`TodoItem__button ${buttonType} ${large ? 'TodoItem__button--large' : ''}`}  onClick={ e => { handleClick()} } >
      <img className="TodoItem__icon" src={img} alt="button" />
    </button >
  )
}

export default TodoItemButton
