import React, { Fragment } from 'react'
import TodoItemButton from './TodoItemButton'

function TodoItemControls({ id, value, done, deleteTodo, editTodo, important, changeImportant }) {
  return (
    <div className="TodoItem__controls">
      {
        done
          ? null
          :
          <Fragment>
            <TodoItemButton
              img='svg/edit.svg'
              buttonType='TodoItem__button--edit'
              handleClick={editTodo}
            />

            <TodoItemButton
              img='svg/important.svg'
              buttonType='TodoItem__button--important'
              handleClick={() => {
                changeImportant(id, value, done, !important)
              }}
            />
          </Fragment>
      }
      <TodoItemButton
        large={done}
        img='svg/trash.svg'
        buttonType='TodoItem__button--trash'
        handleClick={() => {
          deleteTodo(id)
        }}
      />
    </div>
  );
}

export default TodoItemControls
