import React, { Component } from 'react';
import './App.css';
import TodoAdd from '../TodoAdd';
import TodoFilter from '../TodoFilter';
import TodoList from '../TodoList';


class App extends Component {

  constructor(props) {
    super();

    if (!localStorage.getItem('todos')) {
      localStorage.setItem('todos', JSON.stringify([
        this.createTodo(`Выполненная`, true, false, Math.random()),
        this.createTodo(`Невыполненная`, false, false, Math.random()),
        this.createTodo(`Важная`, false, true, Math.random()),
        this.createTodo(`React TodoList`, true, false, Math.random()),
        this.createTodo(`Найти работу`, false, false, Math.random()),
        this.createTodo(`Сделать фильтр`, true, false, Math.random()),
        this.createTodo(`Поспать`, false, true, Math.random()),
        this.createTodo(`Сдохнуть`, false, true, Math.random())
      ])
      );
    }

    this.state = {
      todos: JSON.parse(localStorage.getItem('todos')),
      filter: () => {
        return this.state.todos;
      }
    }
  }

  render() {
    return (
      <div className="App">
        <TodoAdd addTodo={this.addTodo} />
        <TodoFilter
          filter={this.changeFilter}
          todos={this.state.todos}
        />
        <TodoList todos={this.state.filter(this.state.todos)} deleteTodo={this.deleteTodo} editTodo={this.editTodo} />
      </div>
    );
  }

  saveToStorage = (todos) => {
    localStorage.setItem('todos', JSON.stringify(todos));
  }

  changeFilter = (filter) => {
    this.setState({ filter })
  }

  addTodo = (value) => {
    this.setState((state) => {
      let todos = [...state.todos];
      todos.push(this.createTodo(value, false, false, Math.random()));
      this.saveToStorage(todos);
      return { todos };
    });
  }

  createTodo(value, done, important, id) {
    return {
      value,
      done,
      important,
      id
    }
  }

  editTodo = (id, value, done, important) => {
    this.setState((state) => {
      let todos = [...state.todos].map(item => {
        if (item.id === id)
          return this.createTodo(value, done, important, id);
        else
          return item;
      });
      this.saveToStorage(todos);
      return { todos };
    });
  }

  deleteTodo = (id) => {
    this.setState((state) => {
      let todos = [...state.todos];
      let indexOf = 0;
      todos.forEach((item, index) => {
        if (item.id === id) {
          indexOf = index;
        }
      });
      todos.splice(indexOf, 1);
      this.saveToStorage(todos);
      return { todos }
    });
  }
}

export default App;
