import React, { Component } from 'react'
import './TodoFilter.css'

export default class TodoFilter extends Component {
    render() {
        const { filter } = this.props;

        return (
            <div className="TodoFilter">
                <button className="TodoFilter__button" onClick={e => filter(this.filterAll)}>Все</button>
                <button className="TodoFilter__button" onClick={e => filter(this.filterUndone)}>Невыполненные</button>
                <button className="TodoFilter__button" onClick={e => filter(this.filterDone)}>Выполненные</button>
                <button className="TodoFilter__button" onClick={e => filter(this.filterImportant)}>Важные</button>
            </div>
        )
    }

    filterAll(todos) {
        return todos;
    }

    filterUndone(todos) {
        return todos.filter(item => !item.done);
    }

    filterDone(todos) {
        return todos.filter(item => item.done);
    }

    filterImportant(todos) {
        return todos.filter(item => item.important && !item.done);
    }
}
